/* Button */
.button {
    width: 280px;
    height: 50px;
    border-radius: 10px;
    border-color: white;
    background-color: #ED4D7F;
    opacity: 1;
    display: block;
    color: rgb(255, 255, 255);
    font-size: 20px;
    font-family: "Inter UI Bold";
    cursor: pointer;
}


.button[disabled] {
    opacity: 0.5;
    cursor: not-allowed;
}

.button:hover, .button[focus], .button[active] {
    opacity: 0.8;
}


/* Spinner */
.lds-button-spinner {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 11px;
    margin-right: 11px;
}
.lds-button-spinner div {
    box-sizing: border-box;
    position: absolute;
    width: 22px;
    height: 22px;
    margin: 6px;
    border: 2px solid #fff;
    border-radius: 50%;
    -webkit-animation: lds-button-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-button-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #fff transparent transparent transparent;
}

.lds-button-spinner div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
}
.lds-button-spinner div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
}
.lds-button-spinner div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
}
@-webkit-keyframes lds-button-spinner {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes lds-button-spinner {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


/* Page Container */
.page-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
}

/* Token Icon */
.token-icon { 
    width: 100px;
    height: 100px;
    display: block;
}


/* Common */
.bold {
    font-weight: bold;
}


/* Pending Icon */
.dot-pulse-inner {
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #000;
    cursor: pointer;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.5);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    margin: 10px;
}


@-webkit-keyframes pulse {
    0% {
	      opacity: 0.5;
    }
    100% {
	      opacity: 1;
    }
}
@keyframes pulse {
    0% {
	      opacity: 0.5;
    }
    100% {
	      opacity: 1;
    }    
}


.dot-pulse-middle {
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background:  rgba(0, 0, 0, 0.5);
    cursor: pointer;
    box-shadow:  rgba(0, 0, 0, 0.5);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    margin: 10px;    
}

.dot-pulse-outer {
    display: block;
    margin: 0 auto;
    margin-top: 80px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background:  rgba(0, 0, 0, 0.25);
    cursor: pointer;
    box-shadow:  rgba(0, 0, 0, 0.25);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
}

